.waves {
    display: flex;
    padding: 15px 0px;
    height: 260px;
}

.waves_img {
    width: 230px;
    height: 230px;
    object-fit: cover;
    background-repeat: no-repeat;
}

.waves_img img {
    width: 100%;
    height: 100%;
}


.waves_content {
    margin: 0px 25px;
}

.waves_content h1 {
    width: 350px;
    height: 60px;
    font-size: 18px;
}

.waves_content p {
    width: 300px;
    height: 130px;
    font-size: 15px;
    margin-top: 10px;
}

.date_p {
    color: burlywood;
    position: relative;

}